import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import PhoneInput from 'react-phone-input-2';
import { toast } from 'react-toastify';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';
import { API_AUTH } from '../../config';
import axios from '../../utils/axios';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';

export default function ChangeTelephone() {
    const { t } = useLocales();
    const [phone, setPhone] = useState('');
    const navigate = useNavigate();
    const { user, initialize } = useAuth();
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        setPhone(user?.phone);
    }, [user])

    const handleBack = () => {
        navigate(-1);
    };

    const onSubmit = () => {
        if (phone !== user?.phone) {
            setLoading(true);
            axios.put(API_AUTH.userInfo, { phone })
                .then((res) => {
                    if (res.status === 200) {
                        initialize();
                        navigate(-1);
                    } else
                        toast.warning(res.data?.message || res.data?.data);

                    setLoading(false);
                })
                .catch(() => { setLoading(false); })
                .finally(() => {
                    setLoading(false);
                });
        }
    };

    return (
        <Page title="Profile">
            <PageBanner>
                <div className="container mb-8">
                    <div className="w-full justify-center flex flex-col gap-8 items-center">
                        <div className="flex flex-col gap-8 w-full">
                            <div className="flex flex-col gap-4">
                                <h3 className="text-4xl md:text-[64px]">{t('title.add_phone_num')}</h3>
                                <p className='md:w-1/2 text-stone-400'>{t('description.add_phone_desc')}</p>
                            </div>
                            <div className="flex w-full mt-[10px] gap-4 items-center change-telephone">
                                <PhoneInput
                                    country={'br'}
                                    autoFormat
                                    inputClass="ksdEjn input w-full"
                                    value={phone}
                                    onChange={(v, c, e, formattedValue) => setPhone(formattedValue)}
                                />
                            </div>
                        </div>
                        {/* action buttons */}

                        <div className="w-full mb-8">
                            <div className="w-full flex justify-between">
                                <button className="btn btn-primary px-8 capitalize py-4 font-normal rounded-2xl text-[19px] h-auto btn-outline" onClick={handleBack}>
                                    {t('action.back')}
                                </button>
                                <button className={`btn btn-primary px-8 capitalize font-normal py-4 rounded-2xl text-[19px] h-auto disabled:bg-secondary ${loading && 'loading'}`} onClick={onSubmit} disabled={phone === user?.phone} >
                                    {t('action.continue')}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </PageBanner>
        </Page>
    );
}
